@import "../scss/variables";

/*--Default Body--*/

body {
    &.default-body {
        background: $background;
        &.dark-mode {
            background: $dark-theme;
        }
    }
    &.card-shadow {
        &.default-body {
            background: #eff0f6;
        }
        &.white-body {
            background: $white;
        }
    }
    &.white-body {
        background: $white;
        .card {
            border: 1px solid #e8eaf7;
        }
        &.dark-mode {
            background: #10163a;
            .card {
                border: 1px solid $white-1;
            }
        }
    }
    &.card-shadow.light-dark-body {
        background: #edf3f7;
    }
    &.card-shadow.dark-mode {
        background: #1c2248;
    }
    &.card-shadow .header.top-header {
        box-shadow: none;
        border-bottom: 1px solid #eff0f6;
    }
    &.card-shadow .horizontalMenucontainer .header.top-header {
        box-shadow: none;
        border-bottom: 0px solid #eff0f6;
    }
    &.card-shadow .horizontal-main {
        box-shadow: none;
    }
    &.card-shadow .app-sidebar {
        box-shadow: none;
    }
    &.light-dark-body {
        background: #edf3f7;
        &.dark-mode {
            background: #1c2248;
        }
    }
    &.light-card .card {
        background: #f4f5fc;
    }
    &.card-shadow {
        background: #eff0f6;
        .card {
            box-shadow: none !important;
        }
    }
    &.card-radius .card {
        border-radius: 0;
    }
}


