/*---default Horizontal--*/

.default-horizontal {
    .horizontal-main {
        background: $white;
        background: $white;
    }
    .hor-icon {
        color: #8e98db !important;
        fill: #d2d6f1 !important;
    }
    .horizontalMenu>.horizontalMenu-list>li {
        >a {
            color: $color;
            &.active {
                color: $primary;
                text-decoration: none;
                background: #f4f5fc;
            }
            border-right: 1px solid rgb(239, 240, 246);
        }
        &:last-child>a {
            border-right: 1px solid rgb(239, 240, 246);
        }
        >a {
            &:hover .hor-icon {
                color: $primary !important;
                fill: #d2d6f1;
            }
            &.active .hor-icon,
            &:hover {
                color: $primary !important;
            }
        }
    }
}

